import {getArticles,getTags,getFeedArticles,onFavorite,unFavorite} from '@/api/api'
import {mapState}  from 'Vuex'
export default {
  name: 'home',
  async asyncData ({query,store}){
    const page = Number.parseInt(query.page || 1)
    const limit = 2
    const {tag} = query
    const tab = query.tab || 'global_feed'
    const loadArticles = store.state.user && tab  === 'your_feed'? getFeedArticles: getArticles
    // const {data} = await getArticles({
    //   limit,
    //   offset: (page-1)*limit
    // })
    // const {data:tagData} = await getTags()
    // return {
    //   articles: data.articles,
    //   articlesCount: data.articlesCount,
    //   limit,
    //   page,
    //   tags: tagData.tags
    // }
    const [articleRes,tagsRes]= await Promise.all([
      loadArticles({
        limit,
        offset: (page-1)*limit,
        tag
      }),
      getTags()
    ])
    const {articles,articlesCount} = articleRes.data
    articles.forEach(article => {
      article.favoriteDisabled = false
    });
    const {tags} = tagsRes.data
    return {
      articles,
      articlesCount,
      page,
      limit,
      tags,
      tag,
      tab: query.tab || 'global_feed'
    }
  },
  watchQuery: ['page','tag','tab'], // 动态检测数据变化  更新数据
  computed: { 
    totalPage (){
      return Math.ceil(this.articlesCount/this.limit)
    }
  },
  methods: {
    async onFavorite(article){
      article.favoriteDisabled = true
      if (article.favorited){
        // 已点赞 取消
        await unFavorite(article.slug)
        article.favorited = false
        article.favoritesCount += -1
      } else {
        // 添加
        await onFavorite(article.slug)
        article.favorited = true
        article.favoritesCount += 1
      }
      article.favoriteDisabled = false
    }
  }
}
